<template>
	<div>
        
		
        <div id="wrap" style="background-color:#f6f6f6">
            <!-- header -->
            <header id="sub">
            <div class="header_wrap" style="height: 88.5px;">
                <h1>근조화환</h1>
                <span class="goods_telorder" style="position: relative;"><a href='tel:1688-2734'>전화로 주문하기</a></span>
            </div>
            </header>
            <!-- header -->
            <!-- content -->
            <div id="lnbMenu" class="lnb_menu lnb_menu_main" style="top: 88px;">
                <div class="inner_lnb">
                    <div class="list" style="text-align: center;">
                        <a style="display:inline;margin-right:15px;margin-left:15px" v-for="(type,index) in getShopTypeList" :key="index"  @click="changeType(type)" :class="shopType === type.id?'on':''">{{type.shopClassCodeTitle}}</a>
                    </div>
                </div>
            </div>
            <div class="goodsList flower-first">
                <div class="list_goods">
                    <ul class="list">
                        <li v-for="(shop,index) in getShopList" :key="index" :class="index % 2===1?'cut':''"  @click="detailShop(shop)">
                            <div class="item" style="position: relative;">
                                <div v-if="getShopUse === 'none'" style="position: absolute;width: 100%;height: 100%;background: #000;z-index: 1;opacity: 0.75;text-align: center;color: #fff;">

                                    <p style="position: relative;top: 40%; font-family: 'Nanum Myeongjo';word-break: keep-all;">해당 장례식장은 {{shopTypeName}} 상품을 주문할 수 없습니다.</p>

                                </div>
                                <div class="thumb">
                                    <a class="img"><img v-if="shop.shopFile.length>0" :src="imagePath(shop)"></a>
                                </div>
                                <a class="info">
                                    <span class="name">{{shop.shopTitle}}</span>
                                    <span class="cost" v-if="shop.shopDc!==0">{{comma(shop.shopPrice)}}원</span>
                                    <span class="price">{{comma(computedPrice2(shop))}}<strong style="font-size:14px;">원</strong></span>
                                </a>

                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <!-- //content -->
			<!-- footer -->
		    <ShopFooter/>
			<!-- //footer -->
        </div>

	</div>
</template>


<script>
import  ShopFooter from '@/components/common/ShopFooter'
import myMixin from '@/mixin/mixin'
import { mapGetters } from 'vuex'
export default {
    
	name: 'Shop',  
  mixins: [myMixin], 
  components: { 
		ShopFooter,
	},
	data() {
		return {
			shopDetail: {
				id: null,
				shopTitle: '',
				shopTxt: '',
				shopPrice: 0,
				shopDc: 0,
				shopType: {
					codeSubId: null
				},
				shopFile: [],
				shopReply: [],
				shopQna: []
            },
            shopTypeName:'',
            shopType: 1,
            EcodeId:this.$route.query.EcodeId ? this.$route.query.EcodeId : null,

		}
    },
    mounted(){
        
		if(this.EcodeId!==null){

		}else{
			alert('문제가 발생하였습니다. 확인후 다시 시도해 주세요.')
			this.$router.go(-1)
			return
        }
        const parameter = {
            context: this
        }
		this.$store.dispatch('REQUEST_SHOP_TYPE', parameter)
        //this.requstShopList()
    },
	computed:{
		...mapGetters(['getShopList','getShopTypeList','getShopUse']),
    },
    methods:{
        
		imagePath(shop){
			if(shop.shopFile!== null && shop.shopFile.length>0){
				return shop.shopFile[0].filePath
			}else{
				return ''
			}
		},
		computedPrice2(shop) {
			return shop.shopPrice * (1 - shop.shopDc / 100)
        },
        changeType(type){
            this.shopTypeName = type.shopClassCodeTitle
            this.shopType = type.id
            this.requstShopList()

        },
        requstShopList(){
        this.$store.commit('INIT_SHOP_LIST')
		const parameter = {
			page: false,
            shop: this.shopDetail,
            shopType: this.shopType,
            EcodeId:this.EcodeId
		}
		this.$store.dispatch('REQUEST_SHOP_LIST', parameter)

        },
        
		detailShop(shop) {
            if(this.getShopUse === 'none'){
                return
            }
            
            this.$router.push({
				path:'/shopDetail',
				query:{
                    id:shop.id,
                    EcodeId:this.$route.query.EcodeId ? this.$route.query.EcodeId : null,
				}
            })
            
		},
    }
    
}
</script>